export class Champion {
    constructor(data) {
        this.version = data.version; // Versin del campen
        this.id = data.id; // ID del campen
        this.key = data.key; // Clave nica del campen
        this.name = data.name; // Nombre del campen
        this.title = data.title; // Ttulo del campen
        this.blurb = data.blurb; // Descripcin breve del campen
        this.info = {
            attack: data.info.attack, // Estadstica de ataque
            defense: data.info.defense, // Estadstica de defensa
            magic: data.info.magic, // Estadstica de magia
            difficulty: data.info.difficulty // Dificultad de uso
        };
        this.tags = data.tags; // Etiquetas del campen
        this.partype = data.partype; // Tipo de recurso (por ejemplo, Man, Energa)
        this.stats = {
            hp: data.stats.hp, // Salud base
            mp: data.stats.mp, // Man base
        };

        this.image = null; 
    }

    setImage(sprite) {
       
        this.image = sprite
    }

    getDescription() {
        return `${this.name}, ${this.title}: ${this.blurb}`;
    }

    getStats() {
        return {
            hp: this.stats.hp,
            mp: this.stats.mp,
            attack: this.info.attack,
            defense: this.info.defense,
            magic: this.info.magic,
            difficulty: this.info.difficulty
        };
    }
}
